@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.3) translateY(100px);
  }
  50% {
    opacity: 0.9;
    transform: scale(1.05);
  }
  70% {
    transform: scale(0.95);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes float {
  0%, 100% {
    transform: translateY(0) rotate(0deg);
  }
  33% {
    transform: translateY(-10px) rotate(2deg);
  }
  66% {
    transform: translateY(-5px) rotate(-1deg);
  }
}
@keyframes speechBubble {
  0%, 100% {
    transform: translateY(0) scale(1);
  }
  50% {
    transform: translateY(-5px) scale(1.02);
  }
}
@keyframes wagTail {
  0%, 100% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(10deg);
  }
  75% {
    transform: rotate(-10deg);
  }
}
@keyframes pulseSoft {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.8;
  }
}
@keyframes gradientShift {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}
.animate-fade-in-up {
  animation: fadeInUp 0.8s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}
.animate-bounce-in {
  animation: bounceIn 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55) forwards;
}
.animate-float {
  animation: float 6s ease-in-out infinite;
}
.animate-speech-bubble {
  animation: speechBubble 3s ease-in-out infinite;
}
.animate-wag-tail {
  animation: wagTail 1s ease-in-out infinite;
  transform-origin: bottom center;
}
.animate-pulse-soft {
  animation: pulseSoft 2s ease-in-out infinite;
}
.animate-gradient-shift {
  animation: gradientShift 8s ease infinite;
  background-size: 200% auto;
}
.gradient-text {
  background: linear-gradient(135deg, oklch(62.3% 0.214 259.815) 0%, oklch(62.7% 0.265 303.9) 50%, oklch(65.6% 0.241 354.308) 100%);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}
.glass-card {
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  background: rgba(255, 255, 255, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 8px 32px rgba(31, 38, 135, 0.1);
}
.section-padding {
  padding-top: 6rem;
  padding-bottom: 6rem;
  padding-left: 1rem;
  padding-right: 1rem;
}
@media (min-width: 640px) {
  .section-padding {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}
@media (min-width: 1024px) {
  .section-padding {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background-color: oklch(96.7% 0.003 264.542);
}
::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, oklch(70.7% 0.165 254.624), oklch(71.4% 0.203 305.504));
  border-radius: 9999px;
}
::selection {
  background-color: rgba(oklch(62.3% 0.214 259.815), 0.2);
  color: oklch(21% 0.034 264.665);
}
@media (prefers-reduced-motion: reduce) {
  .animate-fade-in-up,
  .animate-bounce-in,
  .animate-float,
  .animate-speech-bubble,
  .animate-wag-tail,
  .animate-pulse-soft,
  .animate-gradient-shift {
    animation: none;
  }
}
